import { FC, PropsWithoutRef } from 'react';

import { Grid, FieldValueList, ForwardProps } from '@pega/cosmos-react-core';

import { StyledCaseSummaryFields } from './CaseView.styles';
import { CaseSummaryFieldsProps } from './CaseView.types';

const CaseSummaryFields: FC<CaseSummaryFieldsProps & ForwardProps> = ({
  primaryFields,
  secondaryFields
}: PropsWithoutRef<CaseSummaryFieldsProps>) => {
  return (
    <Grid as={StyledCaseSummaryFields} container={{ rowGap: 2 }}>
      <FieldValueList variant='stacked' fields={primaryFields} />
      <FieldValueList variant='inline' fields={secondaryFields} />
    </Grid>
  );
};

export default CaseSummaryFields;
